// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import settings from "./settings"

Vue.config.productionTip = false;
Vue.prototype.$settings = settings; // Vuesettings

import axios, {request} from "axios";

axios.defaults.withCredentials = false // ajaxcookiefalse
Vue.prototype.$axios = axios;  // Vue

// element ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
// 
Vue.use(ElementUI);


// vue-video-player video.js
require('video.js/dist/video-js.css'); // video-js.css  video.js 
require('vue-video-player/src/custom-theme.css'); // custom-theme.css  vue-video-player 
import VideoPlayer from 'vue-video-player'
Vue.use(VideoPlayer);

// css
import "../static/css/reset.css";

// sdk
import "../static/js/gt.js"

// vuex
import store from "./store/index"



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
});



