<template>
  <div class="player">
    <div id="player"></div>
  </div>
</template>

<script>
export default {
  name: "Player",
  data() {
    return {
      vodPlayerJs: 'https://player.polyv.net/resp/vod-player/latest/player.js',
      player: null
    }
  },
  methods: {
    check_login() {
      // 
      let token = localStorage.user_token || sessionStorage.user_token;
      if (!token) {
        this.$alert("").then(() => {
          this.$router.push("/user/login");
        });
        return false; // 
      }
      return token;
    },

    // index.htmlpolyvPlayer.js
    loadPlayerScript(callback) {
      if (!window.polyvPlayer) {
        const myScript = document.createElement('script');
        myScript.setAttribute('src', this.vodPlayerJs);
        myScript.onload = callback;
        document.body.appendChild(myScript);
      } else {
        callback();
      }
    },

    loadPlayer() {
      let jwt_token = this.check_login();
      let user_name = localStorage.user_name || sessionStorage.user_name;
      // 1.
      // 2.
      let vid = this.$route.query.vid;
      let self = this;

      const polyvPlayer = window.polyvPlayer;
      this.player = polyvPlayer({
        wrap: '#player',
        width: document.documentElement.clientWidth - 260, // 
        height: document.documentElement.clientHeight, // 
        forceH5: true,
        vid: vid,
        code: user_name, // 
        // 
        playsafe: function (vid, next) { // token
          self.$axios.get(`${self.$settings.HOST}/course/player/`, {
            params: {
              vid: vid,
            },
            headers: {
              "Authorization": `Bearer ${jwt_token}`
            },
          }).then(function (response) {
            // token
            next(response.data);
          })
        }
      });
    }
  },
  // created ajax/
  // mounted js
  mounted() {
    this.loadPlayerScript(this.loadPlayer);
  },
  computed: {},
  destroyed() {
    if (this.player) {
      this.player.destroy();
    }
  }
}
</script>

<style scoped>

</style>



