<template>
  <div class="box">
    <img src="../../static/image/Loginbg.3377d0c.jpg" alt="">
    <div class="register">
      <div class="register_box">
        <div class="register-title"></div>
        <div class="inp">
          <input v-model="mobile" type="text" @blur="CheckMobile" placeholder="" class="user">
          <div></div>
          <input v-model="password" type="password" placeholder="" class="user">
          <div id="geetest"></div>
          <div class="sms-box">
            <input v-model="sms_code" maxlength="4" type="text" placeholder="" class="user">
            <div class="sms-btn" @click="smsHandle">{{ sms_text }}</div>
          </div>
          <button class="register_btn" @click="registerHander"></button>
          <p class="go_login">
            <router-link to="/user/login"></router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data() {
    return {
      sms_code: "",
      mobile: "",
      password: "",
      sms_text: "",
      is_send_sms: false, // 
    }
  },
  created() {

  },
  methods: {
    CheckMobile() {
      this.$axios.get(`${this.$settings.HOST}/user/mobile/${this.mobile}/`)
        .catch(error => {
          // error.response  undefined
          const response = error.response || {};// response 
          // 
          // let response;
          // if(error.response){
          //   response = error.response;
          // }else{
          //   response = {};
          // }
          let message = '';
          if (response.status) {
            message = {
              400: `: ${response.data && response.data.message || ''}`,
              404: '',
            }[response.status] || message;
          }
          // ()@blur
          this.$message.error(`${message}`);
        })
    },

    registerHander() {
      // 
      this.$axios.post(`${this.$settings.HOST}/user/reg/`, {
        mobile: this.mobile,
        password: this.password,
        sms_code: this.sms_code,
      }).then(res => {

        console.log(res.data);
        localStorage.removeItem('user_token');  // 
        localStorage.removeItem('user_id');
        localStorage.removeItem('user_name');
        sessionStorage.user_token = res.data.access;
        sessionStorage.user_id = res.data.id;
        sessionStorage.user_name = res.data.username;

        let self = this;
        this.$alert("", "", {}, {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        }).then(() => {
          self.$router.push('/');  // thisalertVue
        });

      }).catch(error => {
        let data = error.response.data;
        let message = "";
        for (let key in data) {
          message = data[key][0];
        }  // 
        // let message = " "
        // if(error.response.data.mobile){
        //   message = error.response.data.mobile[0];
        // }
        // if(error.response.data.password){
        //   message = error.response.data.password[0];
        // }
        // if(error.response.data.sms_code){
        //   message = error.response.data.sms_code[0];
        // }
        // if(error.response.data.non_field_errors){
        //   message = error.response.data.non_field_errors;
        // }
        this.$message.error(`${message}`)
      });
    },

    smsHandle() {
      // 
      // 1.
      if (!/1[3-9]\d{9}/.test(this.mobile)) {
        this.$message.error("");
        return false;
      }
      // 2.60
      if (this.is_send_sms) {
        this.$message.error("60");
        return false;
      }
      // 3.ajax
      this.$axios.get(`${this.$settings.HOST}/user/sms/${this.mobile}/`).then(response => {
        console.log(response.data);
        this.is_send_sms = true;
        let interval_time = 60;
        let timer = setInterval(() => {
          if (interval_time <= 1) {
            // 
            clearInterval(timer);
            this.is_send_sms = false; // false
            this.sms_text = "";
          } else {
            interval_time--;
            this.sms_text = `${interval_time}`;
          }
        }, 1000);
        this.is_send_sms = true;
      }).catch(error => {
        this.$message.error(error.response.data.message)
      });

      // ajax
    }
  },

};
</script>

<style scoped>
.box {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.box img {
  width: 100%;
  min-height: 100%;
}

.box .register {
  position: absolute;
  width: 500px;
  height: 400px;
  top: 0;
  left: 0;
  margin: auto;
  right: 0;
  bottom: 0;
  top: -338px;
}

.register .register-title {
  width: 100%;
  font-size: 24px;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
  color: #4a4a4a;
  letter-spacing: .39px;
}

.register-title img {
  width: 190px;
  height: auto;
}

.register-title p {
  font-family: PingFangSC-Regular;
  font-size: 18px;
  color: #fff;
  letter-spacing: .29px;
  padding-top: 10px;
  padding-bottom: 50px;
}

.register_box {
  width: 400px;
  height: auto;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .5);
  border-radius: 4px;
  margin: 0 auto;
  padding-bottom: 40px;
}

.register_box .title {
  font-size: 20px;
  color: #9b9b9b;
  letter-spacing: .32px;
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  justify-content: space-around;
  padding: 50px 60px 0 60px;
  margin-bottom: 20px;
  cursor: pointer;
}

.register_box .title span:nth-of-type(1) {
  color: #4a4a4a;
  border-bottom: 2px solid #84cc39;
}

.inp {
  width: 350px;
  margin: 0 auto;
}

.inp input {
  border: 0;
  outline: 0;
  width: 100%;
  height: 45px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  text-indent: 20px;
  font-size: 14px;
  background: #fff !important;
}

.inp input.user {
  margin-bottom: 16px;
}

.inp .rember {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-top: 10px;
}

.inp .rember p:first-of-type {
  font-size: 12px;
  color: #4a4a4a;
  letter-spacing: .19px;
  margin-left: 22px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  /*position: relative;*/
}

.inp .rember p:nth-of-type(2) {
  font-size: 14px;
  color: #9b9b9b;
  letter-spacing: .19px;
  cursor: pointer;
}

.inp .rember input {
  outline: 0;
  width: 30px;
  height: 45px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  text-indent: 20px;
  font-size: 14px;
  background: #fff !important;
}

.inp .rember p span {
  display: inline-block;
  font-size: 12px;
  width: 100px;
  /*position: absolute;*/
  /*left: 20px;*/

}

#geetest {
  margin-top: 20px;
}

.register_btn {
  width: 100%;
  height: 45px;
  background: #84cc39;
  border-radius: 5px;
  font-size: 16px;
  color: #fff;
  letter-spacing: .26px;
  margin-top: 30px;
}

.inp .go_login {
  text-align: center;
  font-size: 14px;
  color: #9b9b9b;
  letter-spacing: .26px;
  padding-top: 20px;
}

.inp .go_login span {
  color: #84cc39;
  cursor: pointer;
}

.sms-box {
  position: relative;
}

.sms-box .sms-btn {
  position: absolute;
  font-size: 14px;
  letter-spacing: 0.26px; /*  */
  right: 16px;
  top: 10px;
  border-left: 1px solid #484848;
  padding-left: 16px;
  padding-bottom: 4px;
  cursor: pointer;
  background: #ffffff;
  overflow: hidden;
}
</style>



