import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // ,vuedata
    cart_length: 0
  },
  // ,vuemethods
  mutations: {
      add_cart(state, data) {
          state.cart_length = data
      }
  }
})



