<template>
  <div class="login box">
    <img src="../../static/image/Loginbg.3377d0c.jpg" alt="">
    <div class="login">
      <div class="login-title">
        <img src="../../static/image/Logotitle.1ba5466.png" alt="">
        <p>!</p>
      </div>
      <div class="login_box">
        <div class="title">
          <span @click="activeTab=0; login_type=0" :class="{ active: activeTab === 0 }"></span>
          <span @click="activeTab=1; login_type=1" :class="{ active: activeTab === 1 }"></span>
        </div>
        <div class="inp" v-if="login_type===0">
          <input v-model="username" type="text" placeholder=" / " class="user">
          <input v-model="password" type="password" name="" class="pwd" placeholder="">
          <div id="geetest1"></div>
          <div class="rember">
            <p>
              <input type="checkbox" class="no" v-model="remember" name="a"/>
              <span></span>
            </p>
            <p></p>
          </div>
          <button class="login_btn" @click="get_geetest_captcha"></button>
          <p class="go_login">
            <router-link to="/user/reg"></router-link>
          </p>
        </div>
        <div class="inp" v-show="login_type===1">
          <input v-model="username" type="text" placeholder="" class="user">
          <input v-model="password" type="text" class="pwd" placeholder="">
          <button style="margin-top: 10px" id="get_code"></button>
          <button class="login_btn"></button>
          <p class="go_login">
            <router-link to="/user/reg"></router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>export default {
  name: 'Login',
  data() {
    return {
      login_type: 0,
      username: "",
      password: "",
      remember: false,
      activeTab: 0, //  activeTab 
    }
  },

  methods: {
    loginHander() {
      this.$axios.post(`${this.$settings.HOST}/user/login/token/`, {
        username: this.username,
        password: this.password,
      }).then(res => {
        if (this.remember) {
          // 
          sessionStorage.removeItem('user_token'); // 
          sessionStorage.removeItem('user_id');
          sessionStorage.removeItem('user_name');
          sessionStorage.removeItem('user_credit');
          sessionStorage.removeItem('credit_to_money');
          localStorage.user_token = res.data.access;
          localStorage.user_id = res.data.id;
          localStorage.user_name = res.data.username;
          localStorage.user_credit = res.data.user_credit;
          localStorage.credit_to_money = res.data.credit_to_money;
        } else {
          // 
          localStorage.removeItem('user_token');  // 
          localStorage.removeItem('user_id');
          localStorage.removeItem('user_name');
          localStorage.removeItem('user_credit');
          localStorage.removeItem('credit_to_money');

          sessionStorage.user_token = res.data.access;
          sessionStorage.user_id = res.data.id;
          sessionStorage.user_name = res.data.username;
          sessionStorage.user_credit = res.data.user_credit;
          sessionStorage.credit_to_money = res.data.credit_to_money;
        }
        // 
        let self = this;
        this.$alert("", "", {}, {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        }).then(() => {
          // self.$router.push('/');  // thisalertVue
          self.$router.go(-1);
        });

      }).catch(error => {
        const h = this.$createElement;
        this.$message({
          message: h('p', null, [
            h('span', {style: 'color: red'}, '! '),
            h('i', {style: 'color: teal'}, 'dx')
          ])
        });
      })
    },
    handlerPopup(captchaObj) {
      // 

      let self = this;
      // 
      captchaObj.onSuccess(function () {
        var validate = captchaObj.getValidate();
        // 
        self.$axios.post(`${self.$settings.HOST}/user/captcha/`, {
          geetest_challenge: validate.geetest_challenge,
          geetest_validate: validate.geetest_validate,
          geetest_seccode: validate.geetest_seccode,
        }).then(response => {
          if (response.data.status) {
            // 
            self.loginHander();
          }
        }).catch(error => {
          console.log(error.response)
        })
      });
      // idgeetest1
      document.getElementById('geetest1').innerHTML = ''; // 
      captchaObj.appendTo("#geetest1");
      self.captchaObj = captchaObj;  // 
      // http://www.geetest.com/install/sections/idx-client-sdk.html
    },
    get_geetest_captcha() {
      // 
      this.$axios.get(`${this.$settings.HOST}/user/captcha/`, {
        params: {
          username: this.username,  // getparams
        }
      }).then(response => {
        // initGeetest
        // 1
        // 2appendTo
        let data = JSON.parse(response.data)  // json
        console.log(data)
        initGeetest({  //initGeetestmain.js
          gt: data.gt,
          challenge: data.challenge,
          product: "popup", // floatembedpopupPC
          offline: !data.success // 
          // http://www.geetest.com/install/sections/idx-client-sdk.html#config
        }, this.handlerPopup);
      }).catch(error => {
        const h = this.$createElement;
        this.$message({
          message: h('p', null, [
            h('span', {style: 'color: red'}, ''),
            h('i', {style: 'color: teal'}, '')
          ])
        });
      });
    },
  },

};
</script>
<style scoped>
.box {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.box img {
  width: 100%;
  min-height: 100%;
}

.box .login {
  position: absolute;
  width: 500px;
  height: 400px;
  top: 0;
  left: 0;
  margin: auto;
  right: 0;
  bottom: 0;
  top: -338px;
}

.login .login-title {
  width: 100%;
  text-align: center;
}

.login-title img {
  width: 190px;
  height: auto;
}

.login-title p {
  font-family: PingFangSC-Regular;
  font-size: 18px;
  color: #fff;
  letter-spacing: .29px;
  padding-top: 10px;
  padding-bottom: 50px;
}

.login_box {
  width: 400px;
  height: auto;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .5);
  border-radius: 4px;
  margin: 0 auto;
  padding-bottom: 40px;
}

.login_box .title {
  font-size: 20px;
  color: #9b9b9b;
  letter-spacing: .32px;
  border-bottom: 1px solid #e6e6e6;
  display: flex;
  justify-content: space-around;
  padding: 50px 60px 0 60px;
  margin-bottom: 20px;
  cursor: pointer;
}

.login_box .title span {
  padding-bottom: 10px; /*  padding-bottom */
}

.login_box .title span.active {
  color: #4a4a4a;
  border-bottom: 2px solid #ffc210;
}

.inp {
  width: 350px;
  margin: 0 auto;
}

.inp input {
  border: 0;
  outline: 0;
  width: 100%;
  height: 45px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  text-indent: 20px;
  font-size: 14px;
  background: #fff !important;
}

.inp input.user {
  margin-bottom: 16px;
}

.inp .rember {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-top: 10px;
}

.inp .rember p:first-of-type {
  font-size: 12px;
  color: #4a4a4a;
  letter-spacing: .19px;
  margin-left: 22px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  /*position: relative;*/
}

.inp .rember p:nth-of-type(2) {
  font-size: 14px;
  color: #9b9b9b;
  letter-spacing: .19px;
  cursor: pointer;
}

.inp .rember input {
  outline: 0;
  width: 30px;
  height: 45px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  text-indent: 20px;
  font-size: 14px;
  background: #fff !important;
}

.inp .rember p span {
  display: inline-block;
  font-size: 12px;
  width: 100px;
  /*position: absolute;*/
  /*left: 20px;*/

}

#geetest {
  margin-top: 20px;
}

.login_btn {
  width: 100%;
  height: 45px;
  background: #84cc39;
  border-radius: 5px;
  font-size: 16px;
  color: #fff;
  letter-spacing: .26px;
  margin-top: 30px;
}

.inp .go_login {
  text-align: center;
  font-size: 14px;
  color: #9b9b9b;
  letter-spacing: .26px;
  padding-top: 20px;
}

.inp .go_login span {
  color: #84cc39;
  cursor: pointer;
}
</style>




